.services {
    .services__container {
        grid-template-columns: repeat(3, 1fr);
        column-gap: 1.875rem;

        .services__card {
            text-align: center;
            padding: 1.875rem;
            border-radius: var(--border-radius);
            background-color: var(--container-color);
            transition: all 0.3s ease-in-out;

            &:hover {
                transform: translateY(-10px);
            }

            &:nth-child(1) {
                background-color: rgb(108, 108, 229);
                box-shadow: 0px 5px 20px 0px rgb(108, 108, 229);

            }

            &:nth-child(2) {
                background-color: rgb(249, 215, 76);
                box-shadow: 0px 5px 20px 0px rgb(249, 215, 76);

            }

            &:nth-child(3) {
                background-color: rgb(249, 123, 139);
                box-shadow: 0px 5px 20px 0px rgb(249, 123, 139);

            }

            .services__img {
                margin-bottom: 1.25rem;
            }

            .services__title {
                font-size: var(--h2-font-size);
                margin-bottom: 1rem;
                color: #fff;
            }

            .services__description {
                color: #f8f9fa;
            }

            &:nth-child(2) .services__title {
                color: var(--title-color);
            }
            &:nth-child(2) .services__description {
                color: var(--text-color);
            }
            
        }
    }
}

@media screen and (max-width: 1024px) {
    .services {
        .services__container {
            grid-template-columns: repeat(2, 330px);
            justify-content: center;
            row-gap: 1.875rem;
        }
    }
}
@media screen and (max-width: 768px) {
    .services {
        .services__container {
            grid-template-columns: 310px;
        }
    }
}

@media screen and (max-width: 350px) {
    .services {
        .services__container {
            grid-template-columns: 1fr;
        }
    }
}